:root {
  --main-red: #ED3237;
  --main-green: #005A00;
  --dark-grey: #AFAFAF;
  --grey: #F2F2F2;
  --light-grey: #FAFAFA;
}

html {
  box-sizing: border-box;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

html,
body {
  height: 100%;
  position: relative;
  font-size: 16px;
}

body {
  max-width: 100vw;
  overflow-x: hidden;
  font-family: "Segoe", sans-serif;
  font-weight: 400;
  font-style: normal;
  line-height: 1.33;
}

ul,
ol,
li,
p,
h1,
h2,
h3,
h4,
h5,
h6 {
  margin: 0.25rem;
  padding: 0;
}

a {
  text-decoration: none;
}

ul[class] {
  list-style: none;
  padding: 0;
  margin: 0;
}

.container {
  max-width: 120rem;
  padding: 0 10rem;
  width: 100%;
}

.flex {
  display: flex;
}